/* Image Slider css section*/
.slider {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image {
    display:grid;
    height: auto;
    width: 600px;
    border-radius: 10px;

    margin: auto ;
  }
  
  .rightArrow {
    position: absolute;
    top: auto ;
    right: 32px;
    font-size: 2rem;
    color: #000;
    z-index: 10;
    cursor: pointer;
    user-select: none;
  }
  .leftArrow {
    position: absolute;
    top: auto;
    left: 32px;
    font-size: 2rem;
    color: #000;
    z-index: 10;
    cursor: pointer;
    user-select: none;
  }
  
  .slider {
    opacity: 10;
    transition-duration: 1s ease-in-out;
  }
  
  .slider.active {
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.025);
  }