.portfolioBackground {
    background: url('../Components/TestPhotos/PortfolioHeaderImage01.jpeg') center center/cover no-repeat;
    height: 25vh;
    box-shadow: inset 0 0 0 1000px rgba(251, 236, 236, 0.64);
    /* The 3 lines below position the text in the very center (vertical height)*/
    display: flex;
    flex-direction:column;
    justify-content: center; 
    /**/

}
.title > p {
    text-align:center;
}

