/*Section Title*/
/*Big Screen*/
@media screen and (min-width:500px) {
    .title > h1 {
        text-align: center;
        font-size:    4rem;
    }
    
    /*Section Subtitle*/
    .title > p {
        text-align:  center;
        font-size:     3rem;
        margin-top:    10px;
        margin-bottom: 10px;
        margin-left:   10px;
        margin-right:  10px;
    }       
}


/*Small Screen*/
@media screen and (min-width:200px) {
    .title > h1 {
        text-align: center;
        font-size:    2.5rem;
    }
    
    /*Section Subtitle*/
    .title > p {
        text-align:center;
        font-size:     1.5rem;
        margin-top:    10px;
        margin-bottom: 10px;
        margin-left:   10px;
        margin-right:  10px;
    }       
}

